export default angular.module('resTablet', [
  'session',
  'deskworks.state',
  'deskworks.dwAlerts.service',
])

  .factory('resTablet', function(session, state, dwAlerts) {
    return {
      signOut,
      resetParams,
    };

    function signOut() {
      return session.logout()
        .catch(err => dwAlerts.error(err, 'Failed to terminate session.'))
        .then(() => resetParams())
        .then(() => state.setPath('/reservation-tablet-wizard'));
    }

    function resetParams() {
      state.setParam('date', null);
      state.setParam('start', null);
      state.setParam('end', null);
      state.setParam('title', null);
      state.setParam('note', null);
      state.setParam('freq', null);
      state.setParam('duration', null);
      state.setParam('confirm', null);
    }
  });
