export default angular.module('deskworks.ReservationTabletSignInCtrl', [
  'flexForms',
  'deskworks.state',
  'deskworks.dwSignIn',
])

  .controller('ReservationTabletSignInCtrl', function($scope, $translate, state) {
    const $ctrl = this;

    state.setPageTitle($translate.instant('SIGN_IN.PAGE_TITLE'));

    /**
     * dwSignIn:SignIn event listener, proceeds to redirect
     */
    $scope.$on('dwSignIn:SignIn', $event => {
      $event.stopPropagation();
      return state.replace().setPath('/reservation-tablet-wizard/reserve');
    });

    /**
     * Back button with countdown
     */
    $ctrl.goBack = () => state.replace().setPath('/reservation-tablet-wizard');
  });
