import './res-tablet.service';

export default angular.module('deskworks.ReservationTabletCompleteCtrl', [
  'deskworks.state',
  'resTablet',
])

  .controller('ReservationTabletCompleteCtrl', function($translate, state, helper, resTablet) {
    const $ctrl = this;

    state.setPageTitle($translate.instant('RESERVATION_TABLET.COMPLETE.PAGE_TITLE'));

    /**
     * Sign Out
     */
    $ctrl.signOut = () => resTablet.signOut();

    /**
     * Make Another Reservation
     */
    $ctrl.reserve = () => {
      resTablet.resetParams();
      return state.setPath('/reservation-tablet-wizard/reserve');
    };
  });
