export default angular.module('deskworks.ReservationTabletSetupCtrl', [
  'session',
  'deskworks.state',
  'deskworks.Credentials',
])

  .controller('ReservationTabletSetupCtrl', function($scope, $translate, $localStorage, dwAlerts, state, helper,
                                                     session, categories, Credentials) {
    const $ctrl = this;

    state.setPageTitle($translate.instant('RESERVATION_TABLET.SETUP.PAGE_TITLE'));

    $localStorage.reservationTablet = $localStorage.reservationTablet || {};
    const ls = $localStorage.reservationTablet;

    $ctrl.categories = categories;
    if ($ctrl.categories.length) {
      $ctrl.category = ls.categoryId && $ctrl.categories.find(c => c.id === ls.categoryId) || $ctrl.categories[0];
      $ctrl.unit = ls.unitId && $ctrl.category.reservationUnits.find(u => u.id === ls.unitId) || $ctrl.category.reservationUnits[0];
    }

    $ctrl.onCategoryChange = () => $ctrl.unit = $ctrl.category ? $ctrl.category.reservationUnits[0] : null;

    /**
     * Start Wizard
     */
    $ctrl.start = () => {
      if (helper.pointFirstInvalid('#reservationTabletSetup')) return;

      return Credentials.get('$$reservations')
        .catch(err => dwAlerts.error(err, 'Failed to get credentials.'))
        .then(credentials => {
          ls.centerId = state.getCurrentCenterId();
          ls.login = credentials.login;
          ls.password = credentials.password;
          ls.unitId = $ctrl.unit.id;
          ls.unitName = $ctrl.unit.name;
          $localStorage.$apply(); // flush local storage
        })
        .then(() => session.logout())
        .then(() => state.setPath(`/reservation-tablet-wizard`));
    };
  });
