import './wizard.scss';
import niceTimeDirective from 'shared/nice-date-time/nice-time.directive';

export default angular.module('deskworks.ReservationTabletScheduleCtrl', [
  'session',
  'deskworks.state',
  niceTimeDirective.name,
])

  .controller('ReservationTabletScheduleCtrl', function($scope, $translate, $q, $interval, $localStorage,
                                                        dwAlerts, state, helper, session, calendarService) {
    const $ctrl = this;

    $localStorage.reservationTablet = $localStorage.reservationTablet || {};
    const ls = $localStorage.reservationTablet;

    state.setPageTitle($translate.instant('RESERVATION_TABLET.SCHEDULE.PAGE_TITLE', { unit: ls.unitName }));

    /**
     * Reserve
     */
    $ctrl.reserve = () => {
      state.setCurrentCenterId(ls.centerId);
      state.setParam('unit', ls.unitId);
      return state.setPath('/reservation-tablet-wizard/reserve');
    };

    /**
     * Fetch
     */
    $ctrl.fetch = (force) => {
      $interval.cancel($ctrl.timer);

      if ($ctrl.fetching && !force) return; // skip fetching when called by timer

      $ctrl.fetching = true;
      return session.loginSilent(ls.login, ls.password)
        .catch(err => dwAlerts.error(err, 'Failed to log in by system user.'))
        .then(() => calendarService.query(ls.centerId)
          .catch(err => dwAlerts.error(err, 'Failed to get calendar data.')))
        .then(categories => categories
          .reduce((arr, cat) => arr.concat(cat.reservationUnits), [])) // flat units list
        .then(units => {
          const unit = units.find(u => u.id === ls.unitId);
          if (!unit) return dwAlerts.error('Reservation unit not found.');
          return unit;
        })
        .then(unit => {
          $ctrl.unit = $ctrl.unit || { reservations: [] };
          $ctrl.unit.name = unit.name;
          unit.reservations.forEach(res => { // sync reservations: copy/insert existing/new
            if (res.to < Date.now()) return; // skip past reservations
            const index = $ctrl.unit.reservations.findIndex(r => r.id === res.id);
            if (index >= 0) angular.copy(res, $ctrl.unit.reservations[index]);
            else $ctrl.unit.reservations.push(res);
          });
          $ctrl.unit.reservations.forEach((res, idx) => { // sync reservations: remove deleted
            const found = unit.reservations.find(r => r.id === res.id);
            if (!found) $ctrl.unit.reservations.splice(idx, 1);
          });

          ls.unitName = $ctrl.unit.name;
          state.setPageTitle($translate.instant('RESERVATION_TABLET.SCHEDULE.PAGE_TITLE', { unit: ls.unitName }));
        })
        .finally(() => {
          $ctrl.fetching = false;
          $interval.cancel($ctrl.timer);
          $ctrl.timer = $interval($ctrl.fetch, 1000 * 60);
        })
        .finally(() => session.logout());
    };

    $ctrl.fetch();

    $scope.$on('$destroy', () => $interval.cancel($ctrl.timer));
  });
